body {
  font-family: 'Josefin Sans';
  /* background-color: #f1edee; */
  background: rgb(235,234,249);
  background: linear-gradient(90deg, rgba(235,234,249,1) 0%, rgba(116,158,166,1) 45%, rgb(46, 74, 80) 100%);
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 0px;
}

.right-arrow,
.left-arrow {
  cursor: pointer;
  background: transparent;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  border-radius: 4px;
  position: absolute;
  bottom: -20px;
  right: 80px;
  transform: scale(1, 1);
  transition: 0.3s all ease-in-out;
}

.right-arrow {
  right: 125px;
}

.right-arrow:hover,
.left-arrow:hover {
  transform: scale(1.2, 1.2);
}

.react-horizontal-scrolling-menu--wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.detail-image {
  width: 729px;
  height: 742px;
}

.hero-banner-img {
  position: absolute;
  right: 40px;
  top: 0px;
  width: 700px;
  height: 900px;
  margin-top: -230px;
  border-radius: 30px;
}

.bodyPart-card {
  transform: scale(1, 1);
  transition: 0.3s all ease-in-out;
}

.bodyPart-card:hover {
  transform: scale(1.1, 1.1);
  transition: 0.3s all ease-in-out;
  border-radius: 40%;
}

.search-btn:hover {
  color: #fff !important;
  background-color: #f11a1a !important;
  border: 1px solid transparent !important;
  transform: scale(1.15, 1.15);
  transition: 0.3s ease-in;
}

.exercise-card {
  width: 350px;
  height: 445px;
  background: #ffdbac;
  box-shadow: 0 4px 8px 0 rgba(22, 21, 21, 0.4);
  border-bottom-left-radius: 20px;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-bottom: 10px;
  transform: scale(1, 1);
  transition: 0.3s all ease-in-out;
}

.exercise-card img {
  height: 300px;
  border-radius: 50%;
  padding: 25px 10px 5px 10px;
}

.exercise-card:hover {
  transform: scale(1.1, 1.1);
  box-shadow: 20px 12px 16px 8px rgba(0,0,0,0.8);
  border-radius: 20px;
}

.detail-image {
  width: 700px;
  height: 700px;
  border: 10px solid #212121;
  border-radius: 10%;
}

.exercise-video {
  display: flex;
  flex-direction: column;
  gap: 40px;
  width: 350px;
  height: 450px;
  text-decoration: none;
}

.vid-thumbnail {
  border: 10px solid #212121;
}

@media screen and (max-width:1200px) {
  .detail-image {
    width: 300px;
    height: 300px;
  }

  .react-horizontal-scrolling-menu--scroll-container {
    width: 500px;
  }

  .left-arrow,
  .right-arrow {
    position: static !important;
  }

  .hero-banner-img {
    display: none;
  }

  .exercise-card {
    width: 320px;
  }

  .exercise-video {
    width: 320px;
    height: 300px;
  }
}

@media screen and (max-width:400px) {
  .exercise-card {
    width: 280px;
  }
}